body {
  color: #fff;
  background: #1a1a1a;
  margin: 20px;
  font-family: Poppins, sans-serif;
}

h1 {
  text-align: center;
}

.container {
  max-width: 700px;
  margin: 0 auto;
}

.launchpad-grid {
  grid-template-columns: repeat(8, 1fr);
  gap: 10px;
  margin: 20px 0;
  display: grid;
}

#connectMIDI {
  font-weight: 600;
}

.pad {
  aspect-ratio: 1;
  cursor: pointer;
  border: 2px solid #333;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  transition: all .1s;
  display: flex;
  position: relative;
}

.pad:hover {
  opacity: .8;
}

.pad-status {
  color: #fff;
  text-shadow: 1px 1px 2px #000c;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.context-menu {
  z-index: 1000;
  background: #2a2a2a;
  border-radius: 8px;
  min-width: 150px;
  padding: 8px 0;
  position: fixed;
  box-shadow: 0 2px 10px #0000004d;
}

.context-menu-item {
  cursor: pointer;
  color: #fff;
  padding: 8px 16px;
  transition: background-color .2s;
}

.context-menu-item:hover {
  background-color: #3a3a3a;
}

.context-menu-item.danger {
  color: #f44;
}

.context-menu-item.info {
  color: #4caf50;
  cursor: default;
  font-weight: bold;
}

.context-menu-item.info:hover {
  background-color: #0000;
}

.controls {
  background: #333;
  border-radius: 8px;
  margin: 20px 0;
  padding: 20px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  border-radius: 4px;
  margin: 5px;
  padding: 10px 20px;
}

button:hover {
  opacity: .9;
}

.color-picker-modal {
  z-index: 1000;
  background: #2a2a2a;
  border-radius: 8px;
  padding: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-overlay {
  z-index: 999;
  background: #00000080;
  position: fixed;
  inset: 0;
}

.text-danger {
  color: #f52b2b;
}
